ProductPackageFieldTable.propTypes = {
  productPackage: productPackageType,
  t: PropTypes.func.isRequired,
  i18n: PropTypes.shape({
    language: PropTypes.string,
  }).isRequired,
};

ProductPackageFieldTable.defaultProps = {
  productPackage: [],
};

export default withTranslation()(ProductPackageFieldTable);
