import React from 'react';
import PropTypes from 'prop-types';
import { withTranslation } from 'react-i18next';
import Box from '@material-ui/core/Box';

import productPackageType from 'components/__types__/productPackage';
import ProductPackageFieldTable from 'components/product-package-field-table/ProductPackageFieldTable';

const ProductPackageDetails = ({ t, productPackage }) => {
  return (
    <Box>
      <h3>
        {t('common.widgetName', {
          widgetNamePlaceholder: 'Product Package',
        })}
      </h3>
      <ProductPackageFieldTable productPackage={productPackage} />
    </Box>
  );
};

ProductPackageDetails.propTypes = {
  productPackage: productPackageType,
  t: PropTypes.func.isRequired,
};

ProductPackageDetails.defaultProps = {
  productPackage: {},
};

export default withTranslation()(ProductPackageDetails);
